.google-container {
  background-color: #4285f4;
  height: 50px;
  border-radius: 1px;
  font-size: 1.6rem;
  cursor: pointer;
  transition: all ease-out 0.2s;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 25%);
}

.google-container:hover {
  background-color: #5f96ee;
}

.google-container:active {
  transform: translateY(-1px);
}

.google-logo {
  font-size: 2.16rem;

}

.icon-container {
  width: 4.8rem;
  height: 100%;
}

.text {
  margin: 0 auto;
}
