* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Exo 2", sans-serif;
  overflow: hidden; /* Yatay ve dikey kaydırmayı kapat */
}

.container {
  width: 100vw; /* viewport width - viewport'un genişliği */
  height: 100vh; /* viewport height - viewport'un yüksekliği */
  display: flex;
  flex-direction: column;
  background-color: #4e617e;
  align-items: center;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  border-radius: 5px;
  padding: 40px;
  width: 25%;
  margin-top: 80px;
}

button {
  width: 100%;
  height: 50px;
  background-color: #4e617e; /* Görseldeki buton rengi */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  font-size: 20px;
}
